<template lang="pug">
    div.exchange-content
        div.account
            label 账户余额：
            span(v-text="`${user.useable_ticket}${appInit.ticket_name}`")
        div.exchange-list-title 兑换：
        template(v-if="isLoaded")
            template(v-if="list && list.length > 0")
                div.exchange-list.list-block
                    ul
                        li(v-for="item in list")
                            div.item-content
                                div.item-inner
                                    div.item-title
                                        img.icon-diamond(src="../../../assets/img/icon-diamond.png")
                                        span(v-text="item.diamonds")
                                    div.item-after 
                                        span.btn(@click="changeTicket(item)" v-text="`${item.ticket}${appInit.ticket_name}`")
            template(v-else)
                null-data                            
</template>
<script>
import nullData from '@/components/nullData'
import queryString from 'querystring';
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
import { mapGetters } from 'vuex'
export default {
    name: 'profitExchange',
    components: {
        nullData
    },
    data() {
        return {
            user: '',
            list: '',
            isLoaded: false
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_exchange' : this.api.userExchange();
            this.axios.get(axiosUrl).then(res => {
                this.isLoaded = true;
                if (res.data.status == 1) {
                    this.user = res.data;
                    this.list = res.data.exchange_rules;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    Toast(err || '请求失败');
                });
        },
        //- - 秀票兑换
        changeTicket(item) {
            let axiosUrl = this.api.userDoExchange();
            this.axios.post(axiosUrl, queryString.stringify({
                rule_id: item.id,
                ticket: item.ticket
            })).then(res => {
                if (res.data.status == 1) {
                    if (this.user.useable_ticket >= item.ticket) {
                        Toast('兑换成功');
                        this.user.useable_ticket = this.user.useable_ticket - item.ticket;
                    }
                    else {
                        Toast(res.data.error || '秀票不足');
                    }
                }
                else {
                    Toast(res.data.error || '兑换失败');
                }
            })
                .catch(err => {
                    Toast(err || '操作失败');
                });
        }
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        })
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.exchange-content {
    .account {
        height: 2.5rem;
        width: 100%;
        padding: 0 .5rem;
        line-height: 2.5rem;
        color: #4b4b4b;
        font-size: .65rem;
        background-color: #fff;
        label {
            margin-right: .25rem;
        }
        span {
            color: @fc-main;
            font-size: .7rem;
        }
    }
    .exchange-list-title {
        height: 1.2rem;
        line-height: 1.2rem;
        font-size: .65rem;
        color: @fc-light;
        padding: 0 .5rem;
        ;
        background-color: #f7f7f7;
    }
    .list-block {
        margin-top: 0;
        li {
            height: 3rem;
            border-bottom: 1px solid #f7f7f7;
            .item-content {
                height: 100%;
                .item-media {
                    padding-top: .6rem;
                    img {
                        height: .7rem;
                        width: .9rem;
                    }
                }
                .item-inner {
                    .item-title {
                        color: #0e0e0e;
                        font-size: .8rem;
                        display: flex;
                        align-items: center;
                        .icon-diamond {
                            margin-right: .5rem;
                            height: .8rem;
                        }
                    }
                }
                .item-inner:after {
                    background-color: transparent;
                }
                .item-after {
                    .btn {
                        display: block;
                        height: 1.5rem;
                        width: 3.9rem;
                        border: 1px solid @fc-main;
                        border-radius: .75rem;
                        line-height: 1.5rem;
                        text-align: center;
                        color: @fc-main;
                        font-size: .65rem;
                    }
                }
            }
        }
    }
}
</style>


